<template>
  <nav class="navbar navbar-light">
    <div class="container">
      <router-link class="navbar-brand"
        :to="{ name: 'home', params: {} }" >
        conduit
      </router-link>
      <ul v-if="!isAuthenticated" class="nav navbar-nav pull-xs-right">
        <li class="nav-item">
          <router-link class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'home' }">
            Home
          </router-link>
        </li>
         <li class="nav-item">
           <router-link class="nav-link"
             active-class="active"
             exact
             :to="{ name: 'login' }">
             <i class="ion-compose"></i>Sign in
           </router-link>
         </li>
         <li class="nav-item">
           <router-link
             class="nav-link"
             active-class="active"
             exact
             :to="{ name: 'register' }">
             <i class="ion-compose"></i>Sign up
           </router-link>
         </li>
       </ul>
       <ul v-else class="nav navbar-nav pull-xs-right">
         <li class="nav-item">
           <router-link
              class="nav-link"
              active-class="active"
              exact
             :to="{ name: 'home' }">
             Home
           </router-link>
         </li>
         <li class="nav-item">
           <router-link class="nav-link"
             active-class="active"
             exact
             :to="{ name: 'article-edit' }">
             <i class="ion-compose"></i>&nbsp;New Article
           </router-link>
         </li>
         <li class="nav-item">
           <router-link class="nav-link"
             active-class="active"
             exact
             :to="{ name: 'settings' }">
             <i class="ion-gear-a"></i>&nbsp;Settings
           </router-link>
         </li>
         <li class="nav-item">
           <router-link class="nav-link"
             active-class="active"
             exact
             :to="{ name: 'profile', params: { username: userName } }">
             {{userName}}
            </router-link>
        </li>
      </ul>
    </div>
  </nav>
 </template>
<script>
export default {
  name: 'RwvHeader',
  computed: {
    isAuthenticated () {
      return this.$store.state.auth.isAuthenticated
    },
    userName () {
      return this.$store.state.auth.user.username
    }
  }
}
</script>



